<template>
	<div class="selected-opt--wrap">
		<template v-if="selectedConditions.company && selectedConditions.frans && selectedConditions.price">
			<h3 class="sub-header">Ви обрали такі умови:</h3>
			<table class="selected-opt">
				<tr class="selected-opt--tr">
					<th class="selected-opt--td selected-opt--th">Компанія:</th>
					<th class="selected-opt--td selected-opt--th">Франшиза:</th>
					<th class="selected-opt--td selected-opt--th">Ціна:</th>
				</tr>
				<tr class="selected-opt--tr">
					<td class="selected-opt--td">{{selectedConditions.company}}</td>
					<td class="selected-opt--td">{{selectedConditions.frans + " грн"}}</td>
					<td class="selected-opt--td">{{selectedConditions.price}}</td>
				</tr>
			</table>
		</template>
		<template v-else>
			<h3 class="sub2-header">Ви не обрали компанію, поверніться на попередню сторінку та зробіть свій вибір</h3>
		</template>
		<router-link class="router--link" to="/offers">Змінити вибір</router-link>
	</div>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {

			}
		},

		computed: {
			...mapGetters([
				"selectedConditions"
			])
		},

		mounted() {
			if (!(this.selectedConditions.company && this.selectedConditions.frans && this.selectedConditions.price)) {
				this.$emit('notSelected');
			}
		}
	}
</script>

<style lang="less">
.selected-opt {
	display: block;

	&--wrap {
		font-size: 19px;
		margin-bottom: 40px;
		@media only screen and (max-width: 1195px) {
 			font: inherit;
 		}
	}

	&--tr {
		display: inline-block;
		margin-right: 5px;
		margin-top: 10px;
		margin-bottom: 15px;
	}

	&--th {
		font-weight: bold;
	}

	&--td {
		display: block;
		margin-bottom: 7px;
	}
}
</style>




