<template>
	<article class="article">
		<h2 class="header form--header header__no-underline">Заповніть особисті дані для оформлення страхового полісу.</h2>
		<h3 class="sub2-header">Зверніть увагу, всі дані потрібно вводити українською мовою.</h3>

		<form class="form">
			<phys-personal-data v-if="transportData.isLegalEntity === false"></phys-personal-data>

			<entity-personal-data v-if="transportData.isLegalEntity === true"></entity-personal-data>

			<documents v-if="transportData.isLegalEntity === false"></documents>

			<address></address>

			<vehicle></vehicle>

			<selected-conditions></selected-conditions>

			<selected-company @notSelected="setErrorNotSelected()"></selected-company>

			<article class="form--block">
				<label for="agree" class="form--label-cont checkout--no">
					<b class="form--text form--text__bold">
						<input @change="agreeWithRules = !agreeWithRules"
							   id="agree"
							   name="agree"
							   type="checkbox"
							   class="form--check form--radio">
						<span class="form--checkmark"></span>

						Я ознайомився та погоджуюсь з <a href="#" class="form--link">Правилами заповнення даних</a>. І даю згоду на обробку своїх персональних даних.
					</b>
				</label>
			</article>

			<div class="form--submit">
				<input :disabled="!agreeWithRules || !companyIsSelected"
				       class="form--send"
					   type="submit"
					   value="Оформить">
				<span class="form--error" v-if="!companyIsSelected">Ви не обрали компанію</span>
			</div>
		</form>
	</article>
</template>

<script>
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";
	import PhysPersonalData from "./Checkout/PhysPersonalData";
	import EntityPersonalData from "./Checkout/EntityPersonalData";
	import Documents from "./Checkout/Documents";
	import Address from "./Checkout/Address";
	import Vehicle from "./Checkout/Vehicle";
	import SelectedConditions from "./SelectedConditions";
	import SelectedCompany from "./SelectedCompany";

	export default {
		data() {
			return {
				agreeWithRules: false,
				companyIsSelected: true
			}
		},

		computed: {
			...mapGetters([
				"transportData",
				"selectedConditions",
				"clientData"
				]),
			...mapGetters("transport",{
				"transExtraName": "transExtraName"
			})
		},

		methods: {
			setErrorNotSelected() {
				this.companyIsSelected = false
			}
		},

		components: {
			PhysPersonalData,
			EntityPersonalData,
			Documents,
			Address,
			Vehicle,
			SelectedConditions,
			SelectedCompany
		}
	}
</script>



<style lang="less">
.checkout {
	&--input {
		padding: 7px 10px;
		@media only screen and (max-width: 1195px) {
			min-width: auto;
 			width: 140px;
 		}
		&__lot {
			width: 40px;
			min-width: auto;
			@media only screen and (max-width: 1195px) {
				width: 35px;
			}
		}
		&__number {
			width: 100px;
			min-width: auto;
			@media only screen and (max-width: 1195px) {
				width: 80px;
			}
		}
		&__by {
			min-width: 480px;
			@media only screen and (max-width: 1195px) {
				min-width: 340px;
			}
		}
		&__company-type {
			width: 60px;
			min-width: auto;
		}
		&__street-type {
			min-width: 100px;
		}
		&__adress-number {
			width: 70px;
			min-width: auto;
			@media only screen and (max-width: 1195px) {
				width: 60px;
			}
		}
		&__auto-number {
			width: 100px;
			min-width: auto;
			@media only screen and (max-width: 1195px) {
				width: 80px;
			}
		}
		&__chassis {
			@media only screen and (max-width: 1195px) {
				width: 160px;
			}
		}
		&__date {
			padding: 5.75px 10px;
		}
		&__upper {
			text-transform: uppercase;
		}
		&:disabled {
			cursor: not-allowed;
			background-color: #f8f8f8;
		}
	}

	&--row {
		flex-wrap: wrap;
	}

	&--sub-row {
		margin-bottom: 20px;
		width: 100%;
		@media only screen and (max-width: 1195px) {
			margin-bottom: 15px;
		}
	}

	&--wrap {
		display: inline-block;
		margin-right: 10px;
		margin-bottom: 20px;
		@media only screen and (max-width: 1195px) {
			margin-bottom: 15px;
		}
		&__street {
			@media only screen and (max-width: 576px) {
				display: block;
			}
		}
	}

	&--sub-header {
		margin-bottom: 20px;
	}

	&--sub2-header {
		margin-bottom: 5px;
	}

	&--label-cont {
		display: inline-block;
		margin-right: 20px;
		margin-bottom: 20px;
	}

	&--checkmark {
		border-radius: 50%;
		margin-left: 0;
		&::after {
			top: 9px;
			left: 9px;
			width: 7px;
			height: 7px;
			border-radius: 50%;
			border: none;
			background: white;
		}
	}

	&--no {
		margin-top: 10px;
		&__number {
			margin-top: 5px;
		}
	}

	&--mob-row {
		display: inline-block;
		@media only screen and (max-width: 576px) {
			display: block;
			margin-bottom: 10px;
		}
	}
}
</style>



