<template>
	<fieldset class="form--block">
      <legend class="sub-header checkout--sub-header">Адреса</legend>
      <div class="form--row checkout--row">
          <div class="checkout--sub-row">
              <div class="checkout--wrap">
                  <label for="adress-region" class="sub2-header checkout--sub2-header">Область</label>
                  <input :value="clientData.region"
                         @input="changeRegionValue($event.target.value)"
                         id="adress-region"
                         type="text"
                         class="form--input checkout--input"
                         placeholder="Київська"
                         required
                         name="adress-region"
                         pattern="^[А-Яа-яіІєЄїЇґҐ\s]+$"
                         oninvalid="setCustomValidity('Назва області повинна складатись із українских букв')"
                         onchange="try{setCustomValidity('')}catch(e){}">
              </div>
              <div class="checkout--wrap">
                  <label for="adress-city" class="sub2-header checkout--sub2-header">Населенний пункт</label>
                  <input :value="clientData.city"
                         @input="changeCityValue($event.target.value)"
                         id="adress-city"
                         type="text"
                         class="form--input checkout--input"
                         placeholder="Київ"
                         required
                         name="adress-city"
                         pattern="^[А-Яа-яіІєЄїЇґҐ\s]+$"
                         oninvalid="setCustomValidity('Назва населенного пункта повинна складатись із українських букв')"
                         onchange="try{setCustomValidity('')}catch(e){}">
              </div>
              <div class="checkout--wrap checkout--wrap__street">

                  <label for="adress-street-type" class="sub2-header checkout--sub2-header">Тип і назва вулиці</label>
                  <div class="checkout--mob-row">
                  <v-select @input="changeStreetTypeValue($event)"
                            :options="streetTypes"
                            :value="streetType"
                            inputId="adress-street-type"
                            name="adress-street-type"
                            :required="true"
                            placeholder="Тип вулиці"
                            class="form--input checkout--input__street-type">

                    <span slot="no-options">Співпадіння не знайдено</span>
                  </v-select>
                </div>
                  <input :value="street"
                         @input="changeStreetValue($event.target.value)"
                         id="adress-street"
                         class="form--input checkout--input"
                         placeholder="Прилужна"
                         required
                         name="adress-street"
                         pattern="^[А-Яа-яіІєЄїЇґҐ\s]+$"
                         oninvalid="setCustomValidity('Назва вулиці повинна складатись із українських букв')"
                         onchange="try{setCustomValidity('')}catch(e){}">
              </div>
              <div class="checkout--wrap">
                <div class="checkout--wrap">
                    <label for="adress-house" class="sub2-header checkout--sub2-header">Будинок</label>
                    <input :value="clientData.house"
                           @input="changeHouseValue($event.target.value)"
                           id="adress-house"
                           name="adress-house"
                           class="form--input checkout--input checkout--input__adress-number"
                           placeholder="4/15"
                           required>
                </div>
                <div class="checkout--wrap">
                    <label for="adress-flat" class="sub2-header checkout--sub2-header">Квартира</label>
                    <the-mask :value="clientData.flat"
                              @input="changeFlatValue($event)"
                              mask="####"
                              id="adress-flat"
                              class="form--input checkout--input checkout--input__adress-number"
                              placeholder="510"
                              value=""
                              name="adress-flat"
                              pattern= "[0-9]+"
                              oninvalid="setCustomValidity('Номер квартири може складатись тільки з цифр')"
                              onchange="try{setCustomValidity('')}catch(e){}">
                    </the-mask>
                </div>
              </div>
          </div>
      </div>
  </fieldset>
</template>

<script>
	import {mapGetters} from "vuex";
    import {mapActions} from "vuex";
    import {TheMask} from 'vue-the-mask';
    import vSelect from "vue-select";

    export default {
        data() {
            return {
                streetTypes: [
                    "бульвар",
                    "вулиця",
                    "дорога",
                    "квартал",
                    "кордонный перехід",
                    "майдан",
                    "мікрорайон",
                    "набережна",
                    "площа",
                    "провулок",
                    "проїзд",
                    "проспект",
                    "тупик",
                    "узвіз",
                    "шоссе"
                ]
            }
        },

        computed: {
            ...mapGetters([
                "clientData"
                ]),
            ...mapGetters("address",{
                street: "street",
                streetType: "streetType"
            }),
        },

        methods: {
            ...mapActions("address", {
                changeRegionValue: "changeRegionValue",
                changeCityValue: "changeCityValue",
                changeStreetValue: "changeStreetValue",
                changeStreetTypeValue: "changeStreetTypeValue",
                changeHouseValue: "changeHouseValue",
                changeFlatValue: "changeFlatValue"
            })
        },

        components: {
            TheMask,
            vSelect
        }
    }
</script>


<style>
    .v-select {
        padding: 2px;
    }
</style>




